.ExperienceBox {
    width: 100%;
    height: 45%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}


.name {
    font-size: 2.5em;
    font-family:'Lato';
    color: rgb(0, 78, 134);
    margin-top: 2%;
}


.role{
    background-color: aqua;
    font-size: 1.5em;
    color: rgb(0, 78, 134);
    font-family: 'Lato';
}

.describe {
    color:rgb(0, 78, 134);
    width: 50%;
    height: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(228, 241, 255);
    padding: 10px;
    margin-top: 1%;
    font-family:'Lato';
    margin-bottom: 4%;
    border-radius: 10px;
}